import axios from "axios";

// @desc Get All Books
// @route GET http://localhost:9000/books
export const getAllBooks = () => {
  return axios.get("https://692f3d7591e00bafccd70748.mockapi.io/books");
};

// @desc Get Book With Book ID
// @route GET http://localhost:9000/books/:bookId
export const getBook = (bookId) => {
  return axios.get(`https://692f3d7591e00bafccd70748.mockapi.io/books/${bookId}`);
};

// @desc  Create New Book
// @route POST http://localhost:9000/books
export const createBook = (book) => {
  return axios.post("https://692f3d7591e00bafccd70748.mockapi.io/books", book);
};

// @desc  Update Book
// @route PUT http://localhost:9000/books/:bookId
export const updateBook = (book, bookId) => {
  return axios.put(`https://692f3d7591e00bafccd70748.mockapi.io/books/${bookId}`, book);
};

// @desc  Delete Book
// @route DELETE http://localhost:9000/books/:bookId
export const deleteBook = (bookId) => {
  return axios.delete(`https://692f3d7591e00bafccd70748.mockapi.io/books/${bookId}`);
};

